import React from "react";
import { useState, useEffect } from "react";
import { Navigate } from "react-router-dom";
import { auth } from "../FirebaseConfig.js";
import { onAuthStateChanged } from "firebase/auth";
import "../normalize.css";
import "./Mypagehome.css";
import Studygirl from "./images/shigureni-studygirl.png";

const Mypagehome = () => {
  /**
   * 
   * @param {string} user - 
   * @param {string} setUser - 
   */
  const [user, setUser] = useState();
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      setLoading(false);
    });
  }, []);

  return (
    <>
      {!loading && (
        <>
          {!user ? (
            <Navigate to={`/`} />
          ) : (
            <>
              <div className="header-wrapper">
                <h1></h1>
                <img src={Studygirl} alt="studygirl" width={200} height={200} />
                <p>HSK</p>
              </div>
              <div className="bodywhole">
                <div className="row">
                  <a href="/Hsk1">
                    <div className="hsk1">
                      <div className="classstyle">HSK1</div>
                    </div>
                  </a>
                  <a href="/Hsk2">
                    <div className="hsk2">
                      <div className="classstyle">HSK2</div>
                    </div>
                  </a>
                </div>
                <div className="row">
                  <a href="/Hsk3">
                    <div className="hsk3">
                      <div className="classstyle">HSK3</div>
                    </div>
                  </a>
                  <a href="/Hsk4">
                    <div className="hsk4">
                      <div className="classstyle">HSK4</div>
                    </div>
                  </a>
                </div>
                <div className="row">
                  <a href="/Hsk5">
                    <div className="hsk5">
                      <div className="classstyle">HSK5</div>
                    </div>
                  </a>
                  <a href="/Hsk6">
                    <div className="hsk6">
                      <div className="classstyle">HSK6</div>
                    </div>
                  </a>
                </div>
              </div>
            </>
          )}
        </>
      )}
    </>
  );
};

export default Mypagehome;
